body {
    background-color: #121212;
    margin: 0;
    text-align: center;
}

.topbar {
    padding: 15px;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    background: #121212;
}

.topbar input {
    width: 100%;
    box-sizing: border-box;
    font-size: 19px;
    outline: none;
    border: none;
    border-radius: 25px;
    padding: 7px 43px 7px 17px;
    background-color: #333;
    color: #fff;
}

.topbar span {
    position: absolute;
    padding: 6px 13px 6px 5px;
    top: 15px;
    right: 15px;
    border-radius: 0 25px 25px 0;
    cursor: pointer;
    color: #fff;
}

img {
    width: 100%;
    height: auto;
    border-radius: 5px;
    cursor: pointer;
}

h1 {
    font-size: 17px;
    color: #fff;
    width: 100%;
    text-align: center;
    text-decoration: underline;
    cursor: pointer;
}